import React from "react";
import Layout from "./Components/Layout";
import DynamicImage from "./Components/DynamicImage";
import { SEO } from "theme-seo";

const Logos = ({ pageContext }) => {
  const logos = pageContext.logos;
  return (
    <Layout page={pageContext.pageName}>
      <SEO
        title={pageContext.title}
        seoTitle={pageContext.seoTitle}
        description={pageContext.description}
      />
      <div className="flex flex-col items-center lg:flex-row p-20  lg:justify-center">
        {logos &&
          logos.map(
            (logo) =>
              logo.alt !== "" &&
              (logo.src.startsWith("http") ? (
                <img
                  loading={"lazy"}
                  id={logo.id}
                  onClick={(e) => window.open(logo.onClickUrl, "Popup", logo.onClickFeatures)}
                  src={logo.src}
                  className="m-4"
                  style={{
                    backgroundColor: "lightgray",
                    borderRadius: "20px",
                    cursor: "pointer",
                  }}
                  key={logo.alt}
                  alt={logo.alt}
                  width={logo.width}
                  height={logo.height}
                />
              ) : (
                <DynamicImage
                  imageName={logo.src}
                  onClick={(e) => window.open(logo.onClickUrl, "Popup", logo.onClickFeatures)}
                  className="m-4"
                  style={{ backgroundColor: "lightgray", borderRadius: "20px", cursor: "pointer" }}
                  key={logo.alt}
                  alt={logo.alt}
                  width={logo.width}
                  height={logo.height}
                />
              ))
          )}
      </div>
    </Layout>
  );
};

export default Logos;
